* {
  box-sizing: border-box;
}

.columns {
  float: left;
  width: 25%;
  padding: 10px;
}

.price {
  border: 1px solid #eee;
  margin: 0;
  padding: 0;
  list-style-type: none;
  transition: all .3s;
}

.price .advanced {
  width: 100px;
  z-index: 1;
  color: #fff;
  background: #5fcf80;
  padding: 1px 0 3px;
  font-size: 14px;
  position: absolute;
  top: 18px;
  right: -68px;
  transform: rotate(45deg);
}

.price:hover {
  box-shadow: 0 8px 12px #0003;
}

.price .header {
  color: #fff;
  background-color: #111;
  font-size: 25px;
}

.price li {
  text-align: center;
  border-bottom: 1px solid #eee;
  padding: 10px;
}

.price .grey {
  background-color: #eee;
  font-size: 20px;
}

.button {
  color: #fff;
  background: #5fcf80;
  border-radius: 50px;
  padding: 8px 35px;
  font-family: Raleway, sans-serif;
  font-size: 14px;
  font-weight: 600;
  transition: all .3s;
  display: inline-block;
}

@media only screen and (max-width: 600px) {
  .columns {
    width: 100%;
  }
}

input[type="text"], select, textarea {
  width: 100%;
  box-sizing: border-box;
  resize: vertical;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 12px;
}

label {
  padding: 12px 12px 12px 0;
  display: inline-block;
}

input[type="submit"] {
  color: #fff;
  cursor: pointer;
  float: right;
  background-color: #04aa6d;
  border: none;
  border-radius: 4px;
  padding: 12px 20px;
}

.containe {
  background-color: #f2f2f2;
  border-radius: 5px;
  padding: 20px;
}

.col-25 {
  float: left;
  width: 25%;
  margin-top: 6px;
}

.col-50 {
  float: right;
  width: 55%;
  margin-top: 6px;
}

.col-75 {
  float: left;
  width: 75%;
  margin-top: 6px;
}

.row:after {
  content: "";
  clear: both;
  display: table;
}

@media screen and (max-width: 600px) {
  .col-25, .col-75, input[type="submit"] {
    width: 100%;
    margin-top: 0;
  }
}

.hidden {
  display: none;
}

.overlay {
  opacity: 1;
  width: 100%;
  height: 300%;
  -webkit-backdrop-filter: blur(3px);
  backdrop-filter: blur(3px);
  z-index: 5000;
  background-color: #00000061;
  position: absolute;
  top: 0;
  left: 0;
}

.private-container {
  width: 70%;
  z-index: 5010;
  background-color: #fff;
  border-radius: 5px;
  padding: 6rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 3rem 5rem #0000004d;
}

.close-modal {
  color: #333;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 5rem;
  position: absolute;
  top: 1.2rem;
  right: 2rem;
}

